}
.card:hover .card-body {
  right: 0;
}

.card-img {
  width: 100%;
  height: 50vh;
  overflow: hidden;
  object-fit: cover;
  border-radius: 15px;
  color: rgba(255, 255, 255, 0);
}

.card-body {
  width: 100%;
  height: 100%;
  top: 0;
  right: -100%;
  position: absolute;
  background: rgba(39, 39, 39, 0.637);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  border-radius: 15px;
  color: #ffff;
